import { Link } from "react-router-dom";
import logo from '../../../src/images/freshcart-logo.svg'
import { useContext } from "react";
// import { counterContext } from "../../Context/CounterContext";



function Navbar() {
  // let {counter} = useContext(counterContext)
    return <>

    
    <nav className="navbar navbar-expand-lg navbar-light bg-light">
  <div className="container">
    <Link className="navbar-brand" to="/">
    <img src={logo} alt="logo" />
    </Link>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">

      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <Link className="nav-link active" aria-current="page" to="/home">Home </Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link active" to="/product">Product</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link active" to="/category">Category</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link active" to="/brands">Brands</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link active" to="/card">Card</Link>
        </li>
      </ul>


      <ul className="navbar-nav ms-auto mb-2 mb-lg-0 align-items-center">
        <li className="nav-item">
            <i className="fa-brands fa-twitter mx-1"></i>
            <i className="fa-brands fa-facebook mx-1"></i>
            <i className="fa-brands fa-linkedin mx-1"></i>
            <i className="fa-brands fa-youtube mx-1"></i>
        </li>
        <li className="nav-item">
          <Link className="nav-link active" to="/login">Login</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link active" to="/Register">Register</Link>
        </li>
      
      </ul>
    </div>
  </div>
</nav>
    
    </>;
}

export default Navbar;