function Register() {
    let [errMessage , setErrMessage] = useState()
    let navigate = useNavigate()

   async function callRegister(requestBody){
    setErrMessage('')
     let {data} =  await axios.post(`https://ecommerce.routemisr.com/api/v1/auth/signup`, requestBody)
     .catch(err => setErrMessage(err.response.data.message)
     )
        console.log(data);
        if(data.message = 'seccess'){
            navigate('/login')
        }
        
    }

    function registerSubmit(values){
        console.log(values);
        callRegister(values)
        
    }

    let mySchema = Yup.object({
        name:Yup.string().min(3 , 'min lenth is 3').max(10 , 'max lenth is 10').required('name is reqired'),
        email:Yup.string().email('email is not valid').required('email is required'),
        phone:Yup.string().required('phone is required').matches(/^01[0125][0-9]{8}$/ , 'phone is not valid'),
        password:Yup.string().required('password is required').matches(/^[A-Z0-9][a-z0-9]{3,10}$/, 'password not valid'),
        rePassword:Yup.string().required('repassword is required').oneOf([Yup.ref('password')] , 'repassword not valid')
    
    })

   const formik=  useFormik({
    initialValues:{
        name:'Essam',
        email:'',
        password:'',
        rePassword:'',
        phone:'',

    },validationSchema:mySchema,onSubmit:registerSubmit
   })

    return <>
    
   <div className="container w-75 m-auto mt-4">
   <form action="" onSubmit={formik.handleSubmit} >
        <h3>Register Now:</h3>
        {errMessage ? <div className="alert alert-danger">{errMessage}</div> : null}
        <label htmlFor="name">Name:</label>
        <input name="name" value={formik.values.name} onChange={formik.handleChange} onBlur={formik.handleBlur} type="text" id="name" className="form-control mb-2"/>
        {formik.errors.name && formik.touched.name?  <div className="alert alert-danger">{formik.errors.name}</div>:''}

        <label htmlFor="email">Email:</label>
        <input name="email" value={formik.values.email} onChange={formik.handleChange} onBlur={formik.handleBlur} type="email" id="email" className="form-control mb-2"/>
        {formik.errors.email && formik.touched.email?  <div className="alert alert-danger">{formik.errors.email}</div>:''}

        <label htmlFor="phone">Phone:</label>
        <input name="phone" value={formik.values.phone} onChange={formik.handleChange} onBlur={formik.handleBlur} type="tell" id="phone" className="form-control mb-2"/>
        {formik.errors.phone && formik.touched.phone?  <div className="alert alert-danger">{formik.errors.phone}</div>:''}

        <label htmlFor="password">Password:</label>
        <input name="password" value={formik.values.password} onChange={formik.handleChange} onBlur={formik.handleBlur} type="password" id="password" className="form-control mb-2"/>
        {formik.errors.password && formik.touched.password?  <div className="alert alert-danger">{formik.errors.password}</div>:''}

        <label htmlFor="rePassword">rePassword:</label>
        <input name="rePassword" value={formik.values.rePassword} onChange={formik.handleChange} onBlur={formik.handleBlur} type="password" id="rePassword" className="form-control mb-2"/>
        {formik.errors.rePassword && formik.touched.rePassword?  <div className="alert alert-danger">{formik.errors.rePassword}</div>:''}
        <button disabled={!(formik.isValid && formik.dirty)} type="submit" className="btn btn-success">Register</button>
    </form>
   </div>
    
    </>;
}

export default Register;